@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  background: #282a36;
  -webkit-font-smoothing: antialiased;
  color: #f8f8f2;
  padding: 1em;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

body,
input,
button {
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

h5 {
  font-size: 32px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #282a36;
  transition: 0.2s;
}

a:hover {
  color: #f24545;
}

li {
  font-size: 32px;
  margin: 1em 0;
  text-align: center;
  background-color: #f8f8f2;
  padding: 0.5em;
  font-weight: 700;
  border-radius: 4px;
  transition: 0.2s;
}

li:hover {
  background-color: #f1f1f1;
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 5px solid #f24545;
}

